import {Grid, ListItem, ListItemAvatar, ListItemText, Paper, Skeleton} from "@mui/material";

import dgnx_logo from '../images/new_listings/dgnx.jpeg';
import fook_logo from '../images/new_listings/fook.jpeg';
import burnit_logo from '../images/new_listings/burnit.jpeg';
import volt_logo from '../images/new_listings/volt.jpeg';
import okinami_logo from '../images/new_listings/okinami.jpeg';
import fsb_logo from '../images/new_listings/fsb.jpeg';
import babykrakens_logo from '../images/new_listings/babykrakens.jpeg';

export default function AdScroll() {

    const list = [
        babykrakens_logo,
        fsb_logo,
        burnit_logo,
        dgnx_logo,
        volt_logo,
        fook_logo,
        okinami_logo
    ];

    return <Grid item xs={12}>
        <div style={{
            margin: '0 auto',
            display: 'grid',
            gridAutoFlow: 'column',
            gap: '1rem',
            height: 'calc(105px + 1rem)',
            overflowY: 'auto',
            overscrollBehaviorX: 'contain',
            scrollSnapType: 'x mandatory'
        }}>
            {
                list.length === 0 && <>
                {
                    [0,1,2].map(i => {
                        return <Paper style={{scrollSnapAlign: 'start', height: 115}} key={i}>
                            <ListItem style={{
                                width: 200,
                                maxWidth: 'none',
                                objectFit: 'contain'
                            }}>
                                <ListItemAvatar>
                                    <Skeleton variant="circular" width={40} height={40} />
                                </ListItemAvatar>
                                <ListItemText
                                    primary={<Skeleton variant="text" sx={{ fontSize: '1rem' }} />}
                                    secondary={<Skeleton variant="text" sx={{ fontSize: '1rem' }} />}
                                />
                            </ListItem>
                        </Paper>
                    })
                }

                </>
            }

            {
                list.map(image => {
                    return <Paper style={{scrollSnapAlign: 'start', height: 115}} key={image}>
                        <div style={{
                            height: 115,
                            width: 230,
                            maxWidth: 'none',
                            objectFit: 'contain'
                        }}>
                            <img alt="Logo" src={image} height={115} style={{borderRadius: 4}}/>
                        </div>
                    </Paper>
                })
            }
        </div>

    </Grid>
}