import store from "../redux/store";
import Web3 from "web3";
import {makeBatchRequest} from "./promisify";
import {updateBalances, updateInfo, updateStaked, updateTotals} from "../redux/slices/coineusSlice";
import coineus_abi from "../abis/coineus.json";
import coineusone_abi from "../abis/coineusone_abi.json";
import consensus_abi from "../abis/consensus_abi.json";
import dividend_distributor_abi from "../abis/DividendDistributor.json";

export default function getUserBalances(wallet_address) {
    const _store = store.getState();
    const {pairs, rpc} = _store.coineus;

    if (wallet_address === undefined) {
        return;
    }

    getFUSE(pairs, rpc.fuse, wallet_address);
    getAVAX(pairs, rpc.avax, wallet_address);
    getBNB(pairs, rpc.bnb, wallet_address);
    getETH(pairs, rpc.eth, wallet_address);
}

function getFUSE(pairs, rpc, wallet_address) {

    const web3 = new Web3(rpc);
    const contract = new web3.eth.Contract(coineus_abi, "0xc2E299b47398963c618De5b05C6BdeCd4cc64022");
    const cnsone_contract = new web3.eth.Contract(coineusone_abi, "0x7042807d547A2de0720421a6d2f629c9EB43E73D");
    const fusd_contract = new web3.eth.Contract(coineus_abi, '0x249BE57637D8B013Ad64785404b24aeBaE9B098B');
    const volt_contract = new web3.eth.Contract(coineus_abi, '0x34Ef2Cc892a88415e9f02b91BfA9c91fC0bE6bD4');
    const fsb_contract = new web3.eth.Contract(coineus_abi, '0x28344D35203CF225E84f5B75E0d64bA3f2d9cc31');

    const btc_contract = new web3.eth.Contract(coineus_abi, '0x33284f95ccb7B948d9D352e1439561CF83d8d00d');
    const eth_contract = new web3.eth.Contract(coineus_abi, '0xa722c13135930332Eb3d749B2F0906559D2C5b99');
    const bnb_contract = new web3.eth.Contract(coineus_abi, '0x6acb34b1Df86E254b544189Ec32Cf737e2482058');

    const cnsvolt_contract = new web3.eth.Contract(coineus_abi, '0x836c205C49D9Db143f0A79DD384d08895f171C5f');
    const cnseth_contract = new web3.eth.Contract(coineus_abi, '0x5c1bB0b286B5Ba67b776f596aCA6625BAf9CCDd5');
    const cnsbnb_contract = new web3.eth.Contract(coineus_abi, '0x5351B7eB69D81731f8C7A5948C1c0570fBd82f48');


    const coineusValidatorAddress = "0x53e2033F86C1a54bEbAE893DC8C9fAe179b3Faa2";
    const consensusAddress = "0x3014ca10b91cb3D0AD85fEf7A3Cb95BCAc9c0f79";
    const consensusContract = new web3.eth.Contract(consensus_abi, consensusAddress);

    const calls = [
        web3.eth.getBalance(wallet_address),
        contract.methods.balanceOf(wallet_address).call,
        contract.methods.getAccountDividendsInfo(wallet_address).call,
        cnsone_contract.methods.balanceOf(wallet_address).call,
        cnsone_contract.methods.getAccount(wallet_address).call,
        fusd_contract.methods.balanceOf(wallet_address).call,
        volt_contract.methods.balanceOf(wallet_address).call,
        consensusContract.methods.delegatedAmount(wallet_address, coineusValidatorAddress).call,
        fsb_contract.methods.balanceOf(wallet_address).call,

        btc_contract.methods.balanceOf(wallet_address).call,
        eth_contract.methods.balanceOf(wallet_address).call,
        bnb_contract.methods.balanceOf(wallet_address).call,

        cnsvolt_contract.methods.balanceOf(wallet_address).call,
        cnsvolt_contract.methods.getAccountDividendsInfo(wallet_address).call,

        cnseth_contract.methods.balanceOf(wallet_address).call,
        cnseth_contract.methods.getAccountDividendsInfo(wallet_address).call,

        cnsbnb_contract.methods.balanceOf(wallet_address).call,
        cnsbnb_contract.methods.getAccountDividendsInfo(wallet_address).call,
    ];

    makeBatchRequest(web3, calls).then(resp => {
        let [
            fuse_balance,
            cns_balance,
            info,
            cnsone_balance,
            cnsone_info,
            fusd_balance,
            volt_balance,
            staked_fuse_with_coineus,
            fsb_balance,

            btc_balance,
            eth_balance,
            bnb_balance,

            cnsvolt_balance,
            cnsvolt_info,

            cnseth_balance,
            cnseth_info,

            cnsbnb_balance,
            cnsbnb_info
        ] = resp;

        store.dispatch(updateBalances({
            chain: 'fuse',
            contract_address: 'native',
            balance: fuse_balance / (10 ** 18)
        }))

        store.dispatch(updateStaked({
            chain: 'fuse',
            contract_address: 'native',
            staked: staked_fuse_with_coineus / (10 ** 18)
        }))

        let withdrawableDividends = info[3];
        let totalDividends = info[4];
        let secondsUntilAutoClaimAvailable = info[7];

        cns_balance = cns_balance / (10 ** 18);
        withdrawableDividends = withdrawableDividends / (10 ** 18);
        totalDividends = totalDividends / (10 ** 18);

        store.dispatch(updateInfo({
            chain: 'fuse',
            contract_address: "0xc2E299b47398963c618De5b05C6BdeCd4cc64022",
            info: {
                cns_balance,
                withdrawableDividends,
                totalDividends,
                secondsUntilAutoClaimAvailable
            }
        }));

        //coineus one
        const {account, balance, deposits, fees, transfersIn, transfersOut, withdrawals} = cnsone_info;
        store.dispatch(updateInfo({
            chain: 'fuse',
            contract_address: "0x7042807d547A2de0720421a6d2f629c9EB43E73D",
            info: {account, balance, deposits, fees, transfersIn, transfersOut, withdrawals}
        }));

        store.dispatch(updateBalances({
            chain: 'fuse',
            contract_address: "0xc2E299b47398963c618De5b05C6BdeCd4cc64022",
            balance: cns_balance
        }));

        store.dispatch(updateBalances({
            chain: 'fuse',
            contract_address: '0x249BE57637D8B013Ad64785404b24aeBaE9B098B',//fUSD
            balance: fusd_balance / (10 ** 18)
        }));

        store.dispatch(updateBalances({
            chain: 'fuse',
            contract_address: '0x34Ef2Cc892a88415e9f02b91BfA9c91fC0bE6bD4',//fUSD
            balance: volt_balance / (10 ** 18)
        }));

        store.dispatch(updateBalances({
            chain: 'fuse',
            contract_address: "0x7042807d547A2de0720421a6d2f629c9EB43E73D",//CNSONE
            balance: cnsone_balance / (10 ** 18)
        }));

        store.dispatch(updateBalances({
            chain: 'fuse',
            contract_address: '0x28344D35203CF225E84f5B75E0d64bA3f2d9cc31',//fsb
            balance: fsb_balance / (10 ** 9)
        }));


        store.dispatch(updateBalances({
            chain: 'fuse',
            contract_address: '0x33284f95ccb7B948d9D352e1439561CF83d8d00d',//btc
            balance: btc_balance / (10 ** 8)
        }));

        store.dispatch(updateBalances({
            chain: 'fuse',
            contract_address: '0xa722c13135930332Eb3d749B2F0906559D2C5b99',//eth
            balance: eth_balance / (10 ** 18)
        }));

        store.dispatch(updateBalances({
            chain: 'fuse',
            contract_address: '0x6acb34b1Df86E254b544189Ec32Cf737e2482058',//bnb
            balance: bnb_balance / (10 ** 18)
        }));

        store.dispatch(updateBalances({
            chain: 'fuse',
            contract_address: '0x836c205C49D9Db143f0A79DD384d08895f171C5f',//cnsvolt
            balance: cnsvolt_balance / (10 ** 18)
        }));

        withdrawableDividends = cnsvolt_info[3];
        totalDividends = cnsvolt_info[4];
        secondsUntilAutoClaimAvailable = cnsvolt_info[7];

        cnsvolt_balance = cnsvolt_balance / (10 ** 18);
        withdrawableDividends = withdrawableDividends / (10 ** 18);
        totalDividends = totalDividends / (10 ** 18);

        store.dispatch(updateInfo({
            chain: 'fuse',
            contract_address: "0x836c205C49D9Db143f0A79DD384d08895f171C5f",
            info: {
                cnsvolt_balance,
                withdrawableDividends,
                totalDividends,
                secondsUntilAutoClaimAvailable
            }
        }));

        store.dispatch(updateBalances({
            chain: 'fuse',
            contract_address: '0x5c1bB0b286B5Ba67b776f596aCA6625BAf9CCDd5',//cnseth
            balance: cnseth_balance / (10 ** 18)
        }));

        withdrawableDividends = cnseth_info[3];
        totalDividends = cnseth_info[4];
        secondsUntilAutoClaimAvailable = cnseth_info[7];

        cnseth_balance = cnseth_balance / (10 ** 18);
        withdrawableDividends = withdrawableDividends / (10 ** 18);
        totalDividends = totalDividends / (10 ** 18);

        store.dispatch(updateInfo({
            chain: 'fuse',
            contract_address: "0x5c1bB0b286B5Ba67b776f596aCA6625BAf9CCDd5",
            info: {
                cnseth_balance,
                withdrawableDividends,
                totalDividends,
                secondsUntilAutoClaimAvailable
            }
        }));

        store.dispatch(updateBalances({
            chain: 'fuse',
            contract_address: '0x5351B7eB69D81731f8C7A5948C1c0570fBd82f48',//cnsbnb
            balance: cnsbnb_balance / (10 ** 18)
        }));

        withdrawableDividends = cnsbnb_info[3];
        totalDividends = cnsbnb_info[4];
        secondsUntilAutoClaimAvailable = cnsbnb_info[7];

        cnsbnb_balance = cnsbnb_balance / (10 ** 18);
        withdrawableDividends = withdrawableDividends / (10 ** 18);
        totalDividends = totalDividends / (10 ** 18);

        store.dispatch(updateInfo({
            chain: 'fuse',
            contract_address: "0x5351B7eB69D81731f8C7A5948C1c0570fBd82f48",
            info: {
                cnsbnb_balance,
                withdrawableDividends,
                totalDividends,
                secondsUntilAutoClaimAvailable
            }
        }));

        store.dispatch(updateTotals('fuse'));
    })
}

function getAVAX(pairs, rpc, wallet_address) {

    const web3AVAX = new Web3(rpc);
    const fook_contract = new web3AVAX.eth.Contract(coineus_abi, '0x95923F63dA09B4f7520f7C65a31F318D8228B744');
    const usdc_contract = new web3AVAX.eth.Contract(coineus_abi, '0xB97EF9Ef8734C71904D8002F8b6Bc66Dd9c48a6E');
    const wavax_contract = new web3AVAX.eth.Contract(coineus_abi, '0xB31f66AA3C1e785363F0875A1B74E27b85FD66c7');
    const burnit_contract = new web3AVAX.eth.Contract(coineus_abi, '0x9CcAd19669259C1FBF13FDa959400C871b3d0E00');
    const dgnx_contract = new web3AVAX.eth.Contract(coineus_abi, '0x51e48670098173025C477D9AA3f0efF7BF9f7812');
    const babykrakens_contract = new web3AVAX.eth.Contract(coineus_abi, '0xA2Df0Dc89dfc39FAEe6cf13DFBf2203fEA3670E4');
    const btc_contract = new web3AVAX.eth.Contract(coineus_abi, '0x152b9d0FdC40C096757F570A51E494bd4b943E50');

    const burnit_dividends_contract = new web3AVAX.eth.Contract(dividend_distributor_abi, '0x32b82215a5ebb48acf7faadded562bc175716920');

    const callsAVAX = [
        web3AVAX.eth.getBalance(wallet_address),
        fook_contract.methods.balanceOf(wallet_address).call,
        fook_contract.methods.getAccountDividendsInfo(wallet_address).call,
        usdc_contract.methods.balanceOf(wallet_address).call,
        wavax_contract.methods.balanceOf(wallet_address).call,
        burnit_contract.methods.balanceOf(wallet_address).call,
        dgnx_contract.methods.balanceOf(wallet_address).call,
        babykrakens_contract.methods.balanceOf(wallet_address).call,
        babykrakens_contract.methods.getAccountDividendsInfo(wallet_address).call,
        btc_contract.methods.balanceOf(wallet_address).call,

        burnit_dividends_contract.methods.getUnpaidEarnings(wallet_address).call,
        burnit_dividends_contract.methods.shares(wallet_address).call,
        burnit_dividends_contract.methods.shareholderClaims(wallet_address).call,
        burnit_dividends_contract.methods.minPeriod().call,

    ];

    makeBatchRequest(web3AVAX, callsAVAX).then(resp => {
        let [
            avax_balance,
            balance,
            info,
            usdc_balance,
            wavax_balance,
            burnit_balance,
            dgnx_balance,
            babykrakens_balance,
            babykrakens_info,
            btc_balance,

            burnit_unpaid,
            burnit_shares,
            burnit_shareHolderClaims,
            burnit_minPeriod,

        ] = resp;

        store.dispatch(updateBalances({
            chain: 'avax',
            contract_address: 'native',
            balance: avax_balance / (10 ** 18)
        }))

        let withdrawableDividends = info[3];
        let totalDividends = info[4];
        let secondsUntilAutoClaimAvailable = info[7];

        balance = balance / (10 ** 18);
        withdrawableDividends = withdrawableDividends / (10 ** 6);
        totalDividends = totalDividends / (10 ** 6);

        store.dispatch(updateInfo({
            chain: 'avax',
            contract_address: '0x95923F63dA09B4f7520f7C65a31F318D8228B744',
            info: {
                balance,
                withdrawableDividends,
                totalDividends,
                secondsUntilAutoClaimAvailable
            }
        }));

        store.dispatch(updateBalances({
            chain: 'avax',
            contract_address: '0x95923F63dA09B4f7520f7C65a31F318D8228B744', //fook
            balance: balance
        }))

        store.dispatch(updateBalances({
            chain: 'avax',
            contract_address: '0xB97EF9Ef8734C71904D8002F8b6Bc66Dd9c48a6E', //usdc
            balance: usdc_balance / (10 ** 6)
        }))

        store.dispatch(updateBalances({
            chain: 'avax',
            contract_address: '0xB31f66AA3C1e785363F0875A1B74E27b85FD66c7', //wavax
            balance: wavax_balance / (10 ** 18)
        }))

        store.dispatch(updateBalances({
            chain: 'avax',
            contract_address: '0x9CcAd19669259C1FBF13FDa959400C871b3d0E00', //burnit
            balance: burnit_balance / (10 ** 9)
        }))

        let timeToClaim = (parseInt(burnit_shareHolderClaims) + parseInt(burnit_minPeriod)) - (new Date()).getTime()/1000;
        if(timeToClaim < 0) timeToClaim = 0
        store.dispatch(updateInfo({
            chain: 'avax',
            contract_address: '0x9CcAd19669259C1FBF13FDa959400C871b3d0E00',
            info: {
                withdrawableDividends: burnit_unpaid / (10**6),
                totalDividends: burnit_shares[2] / (10**6),
                secondsUntilAutoClaimAvailable: timeToClaim
            }
        }));

        store.dispatch(updateBalances({
            chain: 'avax',
            contract_address: '0x51e48670098173025C477D9AA3f0efF7BF9f7812', //dgnx
            balance: dgnx_balance / (10 ** 18)
        }))

        // BABYKRAKENS TOKEN
        withdrawableDividends = babykrakens_info[3];
        totalDividends = babykrakens_info[4];
        secondsUntilAutoClaimAvailable = babykrakens_info[7];

        babykrakens_balance = babykrakens_balance / (10 ** 18);
        withdrawableDividends = withdrawableDividends / (10 ** 6);
        totalDividends = totalDividends / (10 ** 6);

        store.dispatch(updateInfo({
            chain: 'avax',
            contract_address: '0xA2Df0Dc89dfc39FAEe6cf13DFBf2203fEA3670E4',
            info: {
                babykrakens_balance,
                withdrawableDividends,
                totalDividends,
                secondsUntilAutoClaimAvailable
            }
        }));
        store.dispatch(updateBalances({
            chain: 'avax',
            contract_address: '0xA2Df0Dc89dfc39FAEe6cf13DFBf2203fEA3670E4', //baby krakens
            balance: babykrakens_balance
        }))

        //BTC
        store.dispatch(updateBalances({
            chain: 'avax',
            contract_address: '0x152b9d0FdC40C096757F570A51E494bd4b943E50', //btc
            balance: btc_balance / (10**8)
        }))

        store.dispatch(updateTotals('avax'));

    })
        .catch(console.log)
}

function getBNB(pairs, rpc, wallet_address) {
    const web3BSC = new Web3(rpc);
    const feg_contract = new web3BSC.eth.Contract(coineus_abi, '0xacFC95585D80Ab62f67A14C566C1b7a49Fe91167');

    const callsBSC = [
        web3BSC.eth.getBalance(wallet_address),
        feg_contract.methods.balanceOf(wallet_address).call,
    ];

    makeBatchRequest(web3BSC, callsBSC).then(resp => {
        let [
            bnb_balance,
            balance,
        ] = resp;

        store.dispatch(updateBalances({
            chain: 'bnb',
            contract_address: 'native',
            balance: bnb_balance / (10 ** 18)
        }))

        store.dispatch(updateBalances({
            chain: 'bnb',
            contract_address: '0xacFC95585D80Ab62f67A14C566C1b7a49Fe91167',
            balance: balance / (10 ** 9)
        }))

        store.dispatch(updateTotals('bnb'));

    })
}

function getETH(pairs, rpc, wallet_address) {
    const web3ETH = new Web3(rpc);
    const okinami_contract = new web3ETH.eth.Contract(coineus_abi, '0x1C4853Ec0d55e420002c5eFaBc7Ed8e0bA7A4121');
    const feg_eth_contract = new web3ETH.eth.Contract(coineus_abi, '0x389999216860AB8E0175387A0c90E5c52522C945');

    const callsETH = [
        web3ETH.eth.getBalance(wallet_address),
        okinami_contract.methods.balanceOf(wallet_address).call,
        feg_eth_contract.methods.balanceOf(wallet_address).call,
    ];

    makeBatchRequest(web3ETH, callsETH).then(resp => {
        let [
            eth_balance,
            okinami_balance,
            feg_eth_balance,
        ] = resp;

        store.dispatch(updateBalances({
            chain: 'eth',
            contract_address: 'native',
            balance: eth_balance / (10 ** 18)
        }))

        store.dispatch(updateBalances({
            chain: 'eth',
            contract_address: '0x1C4853Ec0d55e420002c5eFaBc7Ed8e0bA7A4121',
            balance: okinami_balance / (10 ** 9)
        }))

        store.dispatch(updateBalances({
            chain: 'eth',
            contract_address: '0x389999216860AB8E0175387A0c90E5c52522C945',
            balance: feg_eth_balance / (10 ** 9)
        }))

        store.dispatch(updateTotals('eth'));

    })
}