import * as React from "react";
import {ArcSeries, FlexibleWidthXYPlot} from "react-vis";
import {Paper, Typography, ListItem, ListItemIcon, ListItemText, Skeleton} from "@mui/material";
import {makeStyles} from "@mui/styles";
import {Autorenew, Whatshot, Shower, AccountBalance, Bolt, SwapHorizontalCircle} from "@mui/icons-material";
import {CoineusCryptoFormat} from "../utils/currency_format";

const useStyles = makeStyles((theme) => ({
    xyplot: {
        margin: '0 auto',
    },
}));

function BurnSupply(props) {
    const classes = useStyles();

    const {burnt, supply, percent, cnsone, faucet, fund, liquidity, cnsvolt, cnseth, cnsbnb} = props;

    const divider = 360 * .0075 * (Math.PI / 180);

    const aSupply = 360 * percent * (Math.PI / 180);

    const percent2 = cnsone ? (cnsone - burnt) / supply : 0;
    const aCnsone = 360 * percent2 * (Math.PI / 180);

    const burntValue = (percent * 100).toFixed(2);
    const percentStaked = cnsone ? (((cnsone + cnsvolt + cnseth + cnsbnb) / supply) * 100).toFixed(2) : 0;

    const percent3 = faucet / supply;
    const aFaucet = 360 * percent3 * (Math.PI / 180);

    const percent4 = fund / supply;
    const aFund = 360 * percent4 * (Math.PI / 180);

    const percent5 = liquidity / supply;
    const aLiquidity = 360 * percent5 * (Math.PI / 180);

    const percent6 = cnsvolt / supply;
    const aCnsvolt = 360 * percent6 * (Math.PI / 180);

    const percent7 = cnseth / supply;
    const aCnseth = 360 * percent7 * (Math.PI / 180);

    const percent8 = cnseth / supply;
    const aCnsbnb = 360 * percent8 * (Math.PI / 180);

    const colors = {
        label: '#fff',
        supply: '#fbca00',
        burned: '#fa5700',
        staked: '#ff0000',
        faucet: '#1c6fe0',
        fund: '#ff00eb',
        liquidity: '#a100ff',

        cnsvolt: '#65CA00',
        cnseth: '#5F7AE3',
        cnsbnb: '#fbca00'
    }

    if (
        burnt === undefined ||
        supply === undefined ||
        percent === undefined ||
        cnsone === undefined ||
        faucet === undefined ||
        fund === undefined ||
        liquidity === undefined
    ) {
        return <Paper style={{padding: 20}}>
            <Skeleton variant="circular" height={180} width={180} style={{margin: '0 auto'}}/>
        </Paper>;
    }

    return (
        <Paper style={{padding: 20}}>
            <FlexibleWidthXYPlot
                xDomain={[-5, 5]}
                yDomain={[-5, 5]}
                height={250}
                margin={{left: 0, right: 0, top: 0, bottom: 0}}
                className={classes.xyplot}
            >
                <ArcSeries
                    radiusDomain={[0, 10]}
                    center={{x: 0, y: 0}}
                    data={[
                        {
                            angle0: aSupply + aCnsone + aFaucet + aFund + aLiquidity + aCnsvolt + aCnseth + aCnsbnb + (divider * 9),
                            angle: Math.PI * 2,
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.supply,
                        },
                        {
                            angle0: divider,
                            angle: aFaucet + divider,
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.faucet,
                        },
                        {
                            angle0: aFaucet + (divider * 2),
                            angle: aSupply + aFaucet + (divider * 2),
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.burned,
                        },
                        {
                            angle0: aSupply + aFaucet + (divider * 3),
                            angle: aSupply + aCnsone + aFaucet + (divider * 3),
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.staked,
                        },
                        {
                            angle0: aSupply + aCnsone + aFaucet + (divider * 4),
                            angle: aSupply + aCnsone + aFaucet + aCnsvolt + (divider * 4),
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.cnsvolt,
                        },
                        {
                            angle0: aSupply + aCnsone + aFaucet + aCnsvolt + (divider * 5),
                            angle: aSupply + aCnsone + aFaucet + aCnsvolt + aCnseth + (divider * 5),
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.cnseth,
                        },
                        {
                            angle0: aSupply + aCnsone + aFaucet + aCnsvolt + aCnseth + (divider * 6),
                            angle: aSupply + aCnsone + aFaucet + aCnsvolt + aCnseth + aCnsbnb + (divider * 6),
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.cnsbnb,
                        },
                        {
                            angle0: aSupply + aCnsone + aFaucet + aCnsvolt + aCnseth + aCnsbnb + (divider * 7),
                            angle: aSupply + aCnsone + aFaucet + aCnsvolt + aCnseth + aCnsbnb + aFund + (divider * 7),
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.fund,
                        },
                        {
                            angle0: aSupply + aCnsone + aFaucet + aCnsvolt + aCnseth + aCnsbnb + aFund + (divider * 8),
                            angle: aSupply + aCnsone + aFaucet + aCnsvolt + aCnseth + aCnsbnb + aFund + aLiquidity + (divider * 8),
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.liquidity,
                        },
                    ]}
                    colorType={'literal'}
                />
            </FlexibleWidthXYPlot>
            <Typography
                style={{
                    height: 0,
                    width: '100%',
                    textAlign: 'center',
                    top: -125,
                    position: 'relative',
                    fontSize: 30,
                    fontWeight: '700',
                }}
            >
                {burntValue}
                <span
                    style={{
                        fontSize: 12,
                        fontWeight: '400',
                        marginTop: -10,
                        display: 'block',
                    }}
                >
          % BURNED
        </span>
            </Typography>
            {cnsone !== 0 && <Typography
                style={{
                    height: 0,
                    width: '100%',
                    textAlign: 'center',
                    top: -180,
                    position: 'relative',
                    fontSize: 30,
                    fontWeight: '700',
                }}
            >
                {percentStaked}
                <span
                    style={{
                        fontSize: 12,
                        fontWeight: '400',
                        marginTop: -10,
                        display: 'block',
                    }}
                >
          % STAKED
        </span>
            </Typography>}

            <StatListItem icon={<Bolt style={{color: colors.label}}/>} color={colors.label} value={supply}
                          label="TOTAL SUPPLY"/>
            <StatListItem icon={<Shower style={{color: colors.faucet}}/>} color={colors.faucet} value={faucet}
                          label="FAUCET"/>
            <StatListItem icon={<Whatshot style={{color: colors.burned}}/>} color={colors.burned} value={burnt}
                          label="BURNED"/>
            <StatListItem icon={<Bolt style={{color: colors.staked}}/>} color={colors.staked} value={cnsone}
                          label="COINEUS ONE"/>
            <StatListItem icon={<Bolt style={{color: colors.cnsvolt}}/>} color={colors.cnsvolt} value={cnsvolt}
                          label="CNSvolt"/>
            <StatListItem icon={<Bolt style={{color: colors.cnseth}}/>} color={colors.cnseth} value={cnseth}
                          label="CNSeth"/>
            <StatListItem icon={<Bolt style={{color: colors.cnsbnb}}/>} color={colors.cnsbnb} value={cnsbnb}
                          label="CNSbnb"/>
            <StatListItem icon={<AccountBalance style={{color: colors.fund}}/>} color={colors.fund} value={fund}
                          label="FUND STORAGE"/>
            <StatListItem icon={<SwapHorizontalCircle style={{color: colors.liquidity}}/>} color={colors.liquidity}
                          value={liquidity} label="LIQUIDITY"/>
            <StatListItem icon={<Autorenew style={{color: colors.supply}}/>} color={colors.supply}
                          value={supply - cnsone - burnt - faucet - fund - liquidity - cnsvolt - cnseth - cnsbnb} label="CIRCULATING"/>
        </Paper>
    );
}

export default BurnSupply;

function StatListItem(props) {
    const {color, value, label, icon} = props;

    return <ListItem disablePadding>
        <ListItemIcon>
            {icon}
        </ListItemIcon>
        <ListItemText
            primary={CoineusCryptoFormat(value)}
            secondary={label}
            primaryTypographyProps={{fontWeight: 700, style: {color}}}
            secondaryTypographyProps={{fontSize: '.75rem', style: {color}}}
        />
    </ListItem>;
}
